
import { defineComponent } from "vue";
import { prefix } from "@/utils/assist";

export default defineComponent({
    name: `${prefix}Header`,
    props: {
        height: {
            type: String,
            default: "60px",
        },
    },
    computed: {
        getHeight(): string {
            return `height:${this.height};`;
        },
    },
});
